import version from './version?text';
export { version };

function appendPointBranch (group :map, key :string) :void {
	if ( key ) {
		var one :boolean = key.codePointAt(0)===key.charCodeAt(0);
		var char :string = one ? key[0] : key.slice(0, 2);
		var index :number = group.k.indexOf(char);
		if ( index<0 ) {
			var value :map = { k: [], v: [] };
			group.k.push(char);
			group.v.push(value);
		}
		else { value = group.v[index]; }
		appendPointBranch(value, key.slice(one ? 1 : 2));
	}
}

function appendCodeBranch (group :map, key :string) :void {
	if ( key ) {
		for ( var keys :string[] = group.k, index :number = keys.length, char :string = key.charAt(0); ; ) {
			if ( index===0 ) {
				var value :map = { k: [], v: [] };
				group.k.push(char);
				group.v.push(value);
				appendCodeBranch(value, key.slice(1));
				break;
			}
			if ( keys[--index]===char ) {
				appendCodeBranch(group.v[index], key.slice(1));
				break;
			}
		}
	}
}

function sourcify (group :map, needEscape :boolean) :string {
	var branches :string[] = [];
	var singleCharactersBranch :string[] = [];
	for ( var values = group.v, keys = group.k, length = keys.length, index = 0; index<length; ++index ) {
		var char :string = keys[index];
		var sub_branches :string = sourcify(values[index], needEscape);
		if ( needEscape ) {
			switch ( char ) {
				case '*':
				case '+':
				case '?':
				case '^':
				case '$':
				case '(':
				case ')':
				case '[':
				case ']':
				case '{':
				case '|':
				case '-':
				case '.':
				case '\\':
					char = '\\'+char;
			}
		}
		sub_branches ? branches.push(char+sub_branches) : singleCharactersBranch.push(char);
	}
	singleCharactersBranch.length && branches.unshift(singleCharactersBranch.length===1 ? singleCharactersBranch[0] : '['+singleCharactersBranch.join('')+']');
	return branches.length===0 ? '' : branches.length===1 ? branches[0] : '(?:'+branches.join('|')+')';
}

export function groupify (branches :string[], uFlag? :boolean, noEscape? :boolean) :string {
	var group :map = { k: [], v: [] };
	var appendBranch = uFlag ? appendPointBranch : appendCodeBranch;
	for ( var length :number = branches.length, index :number = 0; index<length; ++index ) { appendBranch(group, branches[index]); }
	return sourcify(group, !noEscape);
}

